ngOnInit() {
    this.firstForm = this.fb.group({
      // firstCtrl: ['', Validators.required],
    });

    this.variableType = this.fb.group({
      varType: ['', Validators.required],
    })
    this.secondForm = this.fb.group({
      // secondCtrl: ['', Validators.required],
    });

    this.thirdForm = this.fb.group({
      // thirdCtrl: ['', Validators.required],
    }); 
    this.fourthForm = this.fb.group({});  
    this.fifthForm = this.fb.group({
       condtoshow: ['', ''],
       condtoproc: ['', ''],
       inference: ['', ''],
    });
  }

  onFirstSubmit() {
    this.givenDatasource.getAll().then( (data) => { 
      
      console.log("process List " , JSON.stringify(data));
    });
    // this.firstForm.markAsDirty();
  }

  onSecondSubmit() {
    this.secondForm.markAsDirty();
  }

  onThirdSubmit() {
    this.thirdForm.markAsDirty();
  }

  onFourthSubmit() {
    this.thirdForm.markAsDirty();
  }

  onFifthSubmit() {
    console.log(this.fifthForm.get("condtoshow"));
    this.givenDatasource.getAll().then( (data) => { 
      this.findDataSource.getAll().then( (data1) => {
        this.tipsSource.getAll().then( (data2) => {
          this.formulaetoshowSource.getAll().then( (data3) => {
            this.formulaeinorderSource.getAll().then( (data4) => {
              window.alert("{template: {givendata: {"+JSON.stringify(data)+ 
                                      "}, finddata: {"+JSON.stringify(data1)+
                                      "}, tips: {"+JSON.stringify(data2)+
                                      "}, formulatoshow: {"+JSON.stringify(data3)+
                                      "}, formulatoprocess: {"+JSON.stringify(data4)+
                                      "},condtoshow: "+this.fifthForm.get("condtoshow").value+
                                      ", condtoproc: "+this.fifthForm.get("condtoproc").value+
                                      ", inference:"+this.fifthForm.get("inference").value+"}");
            })
          })
        })
      })      
    })
  }

  onDeleteConfirm(event): void {
    console.log(event);
    if (window.confirm('Are you sure you want to delete?')) {
      console.log(event);
      event.confirm.resolve();
    } else {
      event.confirm.reject();
    }
  }

